
import Link from 'next/link'
import styles from '../Home.module.css'

interface HeaderProps {
  cartCount?: number
}

export default function Header({ cartCount = 0 }: HeaderProps) {
  return (
    <header className={styles.header}>
      <div className={styles.headerInner}>
        <div className={styles.gift}>
          <img src="/images/jeff.gif" alt="gift" width={85} />
        </div>
        <div className={styles.titleWrap}>
          <h1 className={styles.siteTitle}>Another Shitty Page</h1>
        </div>

        <nav className={styles.submenu}>
          <Link href="#"><span className={styles.menuItem}>Cart ({cartCount})</span></Link>
          <Link href="#"><span className={styles.menuItem}>Settings</span></Link>
          <Link href="#"><span className={styles.menuItem}>Wishlist</span></Link>
          <Link href="/login"><span className={styles.menuItem}>Login</span></Link>
        </nav>
      </div>
    </header>
  )
}